'use strict';

// MODULES //

import { isPrimitive as isString } from '@stdlib/assert-is-string';
import { REGEXP as RE_EXTNAME } from '@stdlib/regexp-extname';
import format from '@stdlib/string-format';


// MAIN //

/**
* Returns a filename extension.
*
* @param {string} filename - filename
* @throws {TypeError} must provide a string primitive
* @returns {string} filename extension
*
* @example
* var ext = extname( 'index.js' );
* // returns '.js'
*/
function extname( filename ) {
	if ( !isString( filename ) ) {
		throw new TypeError( format( 'invalid argument. Filename must be a string. Value: `%s`.', filename ) );
	}
	return RE_EXTNAME.exec( filename )[ 1 ];
}


// EXPORTS //

export default extname;
